import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
import { create } from '@/api/common.js'
import { api } from '@/config/common.js'
import getGlobalData from '@/utils/business.js'
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup
	},
	data() {
		return {
			title: '添加产品',
			reqData: {
				productname: '',
				querycode: '',
				price: '',
				productcategory: '',
				unit: '',
				subunit: '',
				unitmultiple: '',
				warningStockQty: '',
				remarks: '',
			},
			disableSubmit: true,
			isMultiUnit: false
		}
	},
	onShow() {
		let pages =  getCurrentPages()
		let curPage = pages[pages.length - 1]
		if (curPage.data.rData) {
			if (curPage.data.rData.key == 'type') {
				this.reqData.productcategory = curPage.data.rData.value
			} else if(curPage.data.rData.key == 'unit') {
				this.reqData.unit = curPage.data.rData.value
			} else if (curPage.data.rData.key == 'subUnit') {
				this.reqData.subunit = curPage.data.rData.value
			}
		}
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		mainHeight() {
			return this.$mainHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleMultiUnitSwitch(val) {
			this.isMultiUnit = val.detail.value
		},
		handleUnitChange() {
			if (this.reqData.unit && this.reqData.subunit) {
				let tmp = this.reqData.unit
				this.reqData.unit = this.reqData.subunit
				this.reqData.subunit = tmp
			}
		},
		handleSubmit() {
			create(api.baseProduct, this.reqData).then(res => {
				if (res.status == 200 && res.data.returnCode == '0000') {
					getGlobalData.getBaseProduct().then(res => {
						getGlobalData.getProductCategory().then(res => {
							uni.navigateBack({
								delta: 1
							})
						}).catch(err => {
							uni.navigateBack({
								delta: 1
							})
						})
					}).catch(err => {
						uni.navigateBack({
							delta: 1
						})
					})
				} else {
					uni.showToast({
						title: '添加失败'
					})
				}
			}).catch(error => {
				uni.showToast({
					title: '添加失败'
				})
			})
		}
	},
	watch:{
		reqData: {
			handler(val) {
				if (val.productname) {
					this.disableSubmit = false
				} else {
					this.disableSubmit = true
				}
			},
			deep: true
		}
	}
}
